
export default (editor, { dc, coreMjmlModel, coreMjmlView }) => {
  const type = 'loop';

  dc.addType(type, {
    isComponent: el => {
      if(el.classList !== undefined) {
        if(el.classList.contains("ark_loop")) {
          console.log("it is a loop");
          return true;
        }
      }
      return false;
    },
    extendFn: ['initToolbar'], // array of model functions to extend
    model: {
      ...coreMjmlModel,
      defaults: {
        name: 'loop',
        highlightable: true,
        resizable: false,
        draggable: '[data-gjs-type=mj-body]',
        droppable: '[data-gjs-type=mj-section]',
        'style-default': {
          'padding-left': '0px',
          'padding-right': '0px',
          'padding-top': '10px',
          'padding-bottom': '10px',
          'text-align': 'center',
        },
        // stylable: [
        //   'text-align',
        //   'padding', 'padding-top', 'padding-left', 'padding-right', 'padding-bottom',
        //   'background-color', 'background-url', 'background-repeat', 'background-size',
        //   'border-radius', 'border-top-left-radius', 'border-top-right-radius', 'border-bottom-left-radius', 'border-bottom-right-radius',
        //   'border', 'border-width', 'border-style', 'border-color'
        // ],
        // 'style-default': {
        //   'background-color': '#414141',
        //   'border-radius': '3px',
        //   'font-size': '13px',
        //   'font-weight': '400',
        //   'color': '#ffffff',
        //   'vertical-align': 'middle',
        //   'padding-top': '10px',
        //   'padding-bottom': '10px',
        //   'padding-right': '25px',npm
        //   'padding-left': '25px',
        //   'align': 'center',
        // },
        // traits: ['href'],

        // 'container-background-color', 'inner-padding'
      },

      /**
       * Rhave to change few things for hte MJML's xml (no id, style, class)
       */
      toHTML() {
        let code = '';
        let model = this;
        let tag = model.get('tagName'),
          sTag = model.get('void');

        // Build the string of attributes
        let strAttr = '';
        let attr = this.getAttrToHTML();
        for (let prop in attr) {
          let val = attr[prop];
          strAttr += typeof val !== undefined && val !== '' ?
            ' ' + prop + '="' + val + '"' : '';
        }

        this.em.trigger("loop:iterations_request", this);
        console.log("AFTER iterations request");
        console.log(this.iterations);


        code += `<${tag}${strAttr}${sTag ? '/' : ''}>` + model.get('content');

        model.get('components').each((model) => {
          code += model.toHTML();
        });

        if (!sTag)
          code += `</${tag}>`;

        return code;
      },
      init() {
        coreMjmlModel.init.call(this);
      }
    },

    view: {
      ...coreMjmlView,
      tagName: 'div',
      attributes: {
        style: 'pointer-events: all;  display: table; width: 100%',
        'data-type': 'mj-wrapper',
      },
      events: {
        // 'select': function () {
        //   // console.log("FOCUS");
        // },
        // 'active': function() {
        //   // console.log("ACTIVE");
        // },
        // 'load': function() {
        //   // console.log("LOAD");
        // },
        // 'click' : function() {
        //   // console.log('click');
        // },
        'mouseenter': function(){
          // console.log("MOUSE ENTER");
        },
        'mouseover': function() {
          // console.log(coreMjmlModel);
          // console.log(coreMjmlView);
          // console.log('mouseover');
        },
        'moouseleave': function() {
          // console.log('mouseleave');
        }
        // You can also make use of event delegation
        // and listen to events bubbled from some inner element
      },

      getMjmlTemplate() {
        return {
          start: `<mjml><mj-body>`,
          end: `</mj-body></mjml>`,
        };
      },
      getChildrenSelector() {
        return 'table tr td';
      },
      // onActive() {
      //   console.log('onActive');
      // },
      renderChildren() {
        coreMjmlView.renderChildren.call(this);
      },
      delayed_render() {
        setTimeout(function(view) {
          view.render(); }, 5, this);
      },
      init() {
        coreMjmlView.init.call(this);
        // this.listenTo(this.model.get('components'), 'add remove', this.delayed_render);
        this.listenTo(this.model.get('components'), 'add remove', function() {
          this.getChildrenContainer().innerHTML = this.model.get('content');
          this.renderChildren();
        });
      },
    },
  });
};
